import {
    TeamsActivityHandler,
    TurnContext,
    MessageFactory,
    CardFactory
} from "botbuilder";

import * as Util from "util";
const TextEncoder = Util.TextEncoder;

import * as debug from "debug";
const log = debug("msteams");
const koImage = "data:image/jpeg;base64,";
const okImage = "data:image/jpeg;base64,";


export class LearningBot extends TeamsActivityHandler {
    private _questions: any[] = [];
    private _answerQuestions: { [key: number]: any } = {}

    private _reviewQuestions: any[] = [];

    constructor() {
        super();
        this._questions = require('./questions.json');

        this.onMessage(async (context: TurnContext, next: () => Promise<void>) => {

            if (context.activity.value) {
                switch (context.activity.value.cardAction) {
                    case "update":
                        await this.updateCardActivity(context);
                        break;
                }
            }
            if (context.activity.text) {
                const botMessageText: string = context.activity.text.trim().toLowerCase();
                console.log(`message = '${botMessageText}'`);
                if (botMessageText === "pregunta") {
                    await this.handleAskMe(context);
                }
                else if (botMessageText === "qué debo repasar") {
                    await this.handleReview(context);
                }
            }
            await next();
        });
    }

    private async handleAskMe(context: TurnContext): Promise<void> {
        const questionIndex = Math.floor(Math.random() * this._questions.length);
        const question = this._questions.splice(questionIndex, 1)[0];
        this._answerQuestions[question.id] = question;
        await context.sendActivity({ attachments: [this._createCard(question, true)] });
    }
    private async handleReview(context: TurnContext): Promise<void> {
        const card = {
            "type": "AdaptiveCard",
            "version": "1.0",
            "body": [
                {
                    "type": "TextBlock",
                    "text": "Estos son los conceptos que debes repasar",
                    "weight": "Bolder"
                },
                {
                    "type": "RichTextBlock",
                    "inlines": [
                        {
                            "type": "TextRun",
                            "text": this._reviewQuestions.map(q => `- ${q.text}`).join("\n")
                        }
                    ]
                }
            ],
            "$schema": "http://adaptivecards.io/schemas/adaptive-card.json"
        };
        await context.sendActivity({ attachments: [CardFactory.adaptiveCard(card)] });
    }

    private async updateCardActivity(context: TurnContext): Promise<void> {
        const knowsAnswer = context.activity.value.answer;
        const id = context.activity.value.id;
        const question = this._answerQuestions[id];
        if (!knowsAnswer) this._reviewQuestions.push(question);
        await context.updateActivity({ attachments: [this._createCard(question, false, knowsAnswer)], id: context.activity.replyToId, type: 'message' });
    }

    private _createCard = (question: any, isInit: boolean, knowsAnswer?: boolean) => {

        const body: any = [
        {
            "type": "TextBlock",
            "text": question.syllabus,
            "size": "Small",
            "horizontalAlignment": "Right",
            "spacing": "Medium"
        },
        {
            "type": "TextBlock",
            "text": question.text,
            "size": "Large",
            "weight": "Bolder",
            "spacing": "Small",
            "wrap": true
        }];


        let actions = [] as any[];
        if (isInit) {
            actions = [
                {
                    "type": "Action.Submit",
                    "title": "Me la sé!",
                    "data": { cardAction: "update", answer: 1, id: question.id }
                },
                {
                    "type": "Action.Submit",
                    "title": "No me la sé",
                    "data": { cardAction: "update", answer: 0, id: question.id }
                }
            ];
        }
        else {

            const text = knowsAnswer ? "Genial! Esta te la sabes." :
                "Vaya, ésta no te la sabes. La apuntamos.";

            const response = {
                "type": "Container",
                "items": [
                    {
                        "type": "ColumnSet",
                        "columns": [
                            {
                                "type": "Column",
                                "width": "30px",
                                "items": [
                                    {
                                        "type": "Image",
                                        "altText": "",
                                        "width": "30px",
                                        "url": knowsAnswer ? okImage : koImage
                                    }
                                ],
                                "horizontalAlignment": "Center"
                            },
                            {
                                "type": "Column",
                                "width": "stretch",
                                "items": [
                                    {
                                        "type": "TextBlock",
                                        "text": text,
                                        "size": "Large",
                                        "height": "stretch"
                                    }
                                ],
                                "horizontalAlignment": "Center"
                            }
                        ],
                        "horizontalAlignment": "Center"
                    }
                ]
            };

            const answer = {
                "type": "RichTextBlock",
                "inlines": [
                    {
                        "type": "TextRun",
                        "text": question.answer
                    }
                ],
                "separator": true
            };
            body.push(response);
            body.push(answer)
        }

        return CardFactory.adaptiveCard({
            "$schema": "http://adaptivecards.io/schemas/adaptive-card.json",
            "type": "AdaptiveCard",
            "version": "1.0",
            "body": body,
            "actions": actions
        });
    }
}